.slick-slider
	//position: relative
	display: block
	box-sizing: border-box
	-moz-box-sizing: border-box
	-webkit-touch-callout: none
	-webkit-user-select: none
	-khtml-user-select: none
	-moz-user-select: none
	-ms-user-select: none
	user-select: none
	-ms-touch-action: pan-y
	touch-action: pan-y
	-webkit-tap-highlight-color: transparent
.slick-list
	position: relative
	overflow: hidden
	display: block
	margin: 0
	padding: 0
	&:focus
		outline: none
	.slick-loading &
		background: white slick-image-url("ajax-loader.gif") center center no-repeat
	&.dragging
		cursor: pointer
		cursor: hand

.slick-slider .slick-list,
.slick-track,
.slick-slide,
.slick-slide img
	-webkit-transform: translate3d(0, 0, 0)
	-moz-transform: translate3d(0, 0, 0)
	-ms-transform: translate3d(0, 0, 0)
	-o-transform: translate3d(0, 0, 0)
	transform: translate3d(0, 0, 0)

.slick-track
	//position: relative
	// left: 0
	// top: 0
	display: block
	zoom: 1
	&:before,
	&:after
		content: ""
		display: table
	&:after
		clear: both
	.slick-loading &
		visibility: hidden

.slick-slide
	float: left
	height: 100%
	min-height: 1px
	text-align: center 
	img
		display: block
		max-width: 100%
	&.slick-loading .course__main__wrap-backgradient
		display: none
	display: none
	&.dragging img
		pointer-events: none
	.slick-initialized &
		display: block
	.slick-loading &
		visibility: hidden 
	.slick-vertical &
		display: block
		height: auto
		border: 1px solid transparent

.slick-prev,
.slick-next
	z-index: 10
	position: absolute
	top: 57%
	+hide-text
	height: 50px
	width: 20px
	background: none
	font-family: "svgfont"
	font-style: normal
	font-weight: normal
	text-rendering: auto
	speak: none
	line-height: 1
	-webkit-font-smoothing: antialiased
	-moz-osx-font-smoothing: grayscale
	display: inline-block
	&:focus
		outline: none
	&.slick-disabled
		opacity: 0.2
	&:before
		height: 50px
		line-height: 50px
.slick-prev
	left: 30px
	color: #D2D2D2
	&:hover 
		color: $red
	&:before
		content: $icon-left
		display: inline-block
		vertical-align: middle
		font-size: 20px
[class$="--helper-button"] .slick-prev
	left: 10px !important
	top: 55% !important


.slick-next
	right: 30px
	color: #D2D2D2
	&:hover
		color: $red
	&:before
		content: $icon-right
		display: inline-block
		vertical-align: middle
		font-size: 20px
[class$="--helper-button"] .slick-next
	right: 10px !important
	top: 55% !important

