// for page course_more
.course__more
	padding-top: 48px
	@extend %in
	.course__more__inner
		margin-top: 80px
		margin-bottom: 70px
		.photo img
			width: 100%

		.description__out
			.course__more__data-start,
			.course__more__duration,
			.course__more__location,
			.course__more__price
				position: relative
				padding-left: 32px
				padding-bottom: 24px 
				font-size: 15px
				span
					color: black
					font-size: 24px
					text-align: center
					line-height: 1em
				i
					position: absolute
					top: 5px
					left: 0px
					font-size: 17px 
					color: $yellow
	.regisr
		height: auto
		width: 100%
		position: relative
		&:after
			content: ""
			position: absolute
			top: 16px
			left: 0px
			width: 39%
			height: 2px
			background: #C4C4C4
		&:before
			content: ""
			position: absolute
			top: 16px
			right: 0px
			width: 39%
			height: 2px
			background: #C4C4C4
		button
			margin: 0 auto
			display: block
			@extend %btn
			@extend %ripples
			a
				position: relative
				background: $blue
				color: white
				+tr(background .5s)
				&:hover
					background: $yellow

	.hole__course
		padding-top: 96px 
		.hole__description-container
			margin-top: 66px
			text-align: center
			.hole_item
				position: relative
				padding-top: 25px
				margin: 0 50px 50px 50px
				display: inline-block 
				width: 265px
				vertical-align: top
				&:after
					content: ""
					position: absolute
					top: 0
					left: calc(50% - 25px)
					width: 50px
					height: 2px  
					border-radius: 2px
					background: $yellow
					
	.course__posibility
		padding-top: 56px 
		.posibility__description-container
			margin-top: 45px
			text-align: center
			
			.posibility_item
				position: relative
				padding-top: 0px
				margin: 0 50px 50px 50px
				display: inline-block 
				width: 265px
				vertical-align: top
				.item-desc
			
				span
					width: 100%
					display: block
					font-size: 50px
					height: 93px
					color: $yellow
	.module__course
		padding-top: 34px
		.module__course__wrap-slider
			height: 100%
			padding-top: 30px 
			.slider__item
				width: 262px
				margin-right: 20px
				height: 100%
				display: inline-block
				vertical-align: top
				.module__course__main__wrap-backgradient
					position: relative
					z-index: 1
					width: 97%
					height: 100%
					display: inline-block
					&:before
						content: ""
						+coverdiv
						z-index: -1
						background-size: 1134.7px 235.8px
						background-image: url('../img/background.svg')
						transform: translate(5px, 5px)
						background-repeat: repeat
					.module__course__main__block
						z-index: 10
						background-color: white
						padding: 20px
						border: 2px solid $yellow
						border-radius: 2px
						height: 100%
						overflow: hidden

						.module__course__title
							padding: 0px 0 10px 0
							box-shadow: 0px 2px 0px 0px #AAA

						.module__course__content
							padding-top: 10px
							li
								list-style-type: disc
								list-style-position: inside
	.course__more__download
		padding-top: 50px
		img
			height: 70px 
			display: inline-block
			vertical-align: top
		.download
			display: inline-block
			vertical-align: middle
			margin-left: 10px
			color: $grey
			a
				color: $blue
				+fb
				&:hover
					color: $yellow
